import {
    CSSResultArray,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
    style,
    unsafeCSS,
} from '@umetrics/sartorius-ui-base';

export const paginationStyles: GenerateStyles = (tagName) => {
    const { root, child } = cssSelector(tagName);

    const expand_duration = cssVariableAsVar('expand_transition_duration');

    return [
        style,
        unsafeCSS(`
        :host,
        :host::before,
        :host::after,
        :host *,
        :host *::before,
        :host *::after {
            box-sizing: border-box;
        }

        ${root} {
            display: flex;
            width: 100%;
            align-items: center;
            justify-content: flex-end;
            font-size: 16px;
        }

        ${child('rows-text')} {
            font-size: 14px;
            white-space: nowrap;
            width: auto;
            margin-right: 10px;
            color: ${cssVariableAsVar('color_secondary_8')}
        }
        ${child('page')} {
            --button-padding: 10px;
            --button-min-width: 36px;
        }
        ${child('page-selected')} {
            --button-padding: 10px;
            --button-min-width: 36px;
            transition: ${expand_duration};
            --button-cursor: default;
            background-color: black;
            --button-text-color: ${cssVariableAsVar('color_primary_2')};
            --button-hover-text-color: ${cssVariableAsVar('color_primary_2')};
        }

        ${child('page-size-selected')} {
            --button-padding: 10px;
            transition: ${expand_duration};
            --button-cursor: default;
            background-color: black;
            --button-text-color: ${cssVariableAsVar('color_primary_2')};
            --button-hover-text-color: ${cssVariableAsVar('color_primary_2')};
        }

        ${child('page-container')} {
            display: flex;
            flex-direction: column;
        }
        
        ${child('page-button')} {
            margin-right: 20px;
        }

        ${child('ellipsis')} {
            height: 36px;
            width: 36px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        ${child('pagination-container')} {
            display: flex;
            flex-direction: row;
            background-color: ${cssVariableAsVar('color_secondary_1')};
        }

        /* Code working only for Safari */
        @media not all and (min-resolution:.001dpcm) { 
            @supports (-webkit-appearance:none) {
                ${child('button-arrow')}:focus {
                    outline: none;
                    border: 1px solid ${cssVariableAsVar('color_primary_3')};
                }   
            }
        }
        `),
    ] as CSSResultArray;
};
