import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    ifDefined,
    property,
    state,
} from '@umetrics/sartorius-ui-base';
import { PaginationProperties } from './paginationProperties';
import { paginationStyles } from './paginationStyle';
import { version } from './metadata';

import back from '@umetrics/sartorius-ui-icon/dist/icons/back';
import down from '@umetrics/sartorius-ui-icon/dist/icons/down';
import next from '@umetrics/sartorius-ui-icon/dist/icons/next';
import { PaginationEvent } from './paginationEvent';

@defineElement({
    tagName: 'pagination',
    styles: paginationStyles,
    version,
    //sub: [Icon, TextField, Tooltip, Button, Dropdown],
})
export class Pagination extends BaseElement implements PaginationProperties {
    // Static members
    private static Icon;
    private static Button;
    private static ButtonType;
    private static ButtonSlot;
    private static Dropdown;

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public disabled = false;
    @property({ type: String, attribute: 'rows-text' })
    rowsText = '';

    @property({ type: Number, attribute: 'rows' })
    rows;
    @property({ type: Number, attribute: 'page' })
    page = 1;
    @property({ type: Number, attribute: 'page-size' })
    pageSize = 20;
    @property({ type: Boolean, attribute: 'manual-page-size', converter: booleanConverter, reflect: true })
    manualPageSize = false;

    //Private members
    @state()
    private _pagesNumber = 0;
    @state()
    private _open = false;

    private readonly _minRows: number;
    private readonly _lowMidRows: number;
    private readonly _midRows: number;
    private readonly _highMidRows: number;
    private readonly _maxRows: number;

    constructor() {
        super();
        this._minRows = 10;
        this._lowMidRows = 20;
        this._midRows = 50;
        this._highMidRows = 100;
        this._maxRows = 300;
    }

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });

        imports.push({
            module: () => import('@umetrics/sartorius-ui-button'),
            elem: ['Button', 'ButtonType', 'ButtonSlot'],
        });
        imports.push({ module: () => import('@umetrics/sartorius-ui-dropdown'), elem: ['Dropdown'] });

        Pagination.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    protected update(changedProperties) {
        super.update(changedProperties);

        if (changedProperties.has('rows') || changedProperties.has('pageSize')) {
            this._pagesNumber = Math.ceil(this.rows / this.pageSize);
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            disabled: this.disabled,
        });

        const manualPageSizeHtml = this.manualPageSize
            ? html`<p class="${baseClassName}-rows-text">${this.rowsText}</p>
                  ${this._renderPageSize(baseClassName)}`
            : null;

        return html`
            <section class="${rootClasses}">
                ${manualPageSizeHtml}
                <div class="${baseClassName}-prev-icon-container">
                    ${this._renderArrowButton(baseClassName, this._previousPage, back, this.page <= 1)}
                </div>
                <div class="${baseClassName}-pagination-container">${this._renderPages(baseClassName)}</div>
                <div class="${baseClassName}-next-icon-container">
                    ${this._renderArrowButton(baseClassName, this._nextPage, next, this.page >= this._pagesNumber)}
                </div>
            </section>
        `;
    }

    private _renderPageSize(baseClassName) {
        return html`<${Pagination.Button.tag} class="${baseClassName}-page-button" id="dropdown-toggle" type="${
            Pagination.ButtonType.Tertiary
        }" button-size="small" disabled="${this.disabled}">${this.pageSize}
            <${Pagination.Icon.tag}
            slot="${Pagination.ButtonSlot.ICON}" 
            icon=${down}></${Pagination.Icon.tag}>
        </${Pagination.Button.tag}>
        <${Pagination.Dropdown.tag} @close="${() => (this._open = false)}" @toggle="${
            this._toggle
        }" click-item-close open="${this._open}" placement="below-left" object-id="dropdown-toggle">
            <div class="${baseClassName}-page-container">
            <${Pagination.Button.tag} class="${baseClassName}-page-size${
                this.pageSize === this._minRows ? '-selected' : ''
            }" @click="${() => this._updatePagesize(this._minRows)}" button-size="small" type="${
                Pagination.ButtonType.Tertiary
            }" full-width>${this._minRows}</${Pagination.Button.tag}>
            <${Pagination.Button.tag} class="${baseClassName}-page-size${
                this.pageSize === this._lowMidRows ? '-selected' : ''
            }" @click="${() => this._updatePagesize(this._lowMidRows)}" button-size="small" type="${
                Pagination.ButtonType.Tertiary
            }" full-width>${this._lowMidRows}</${Pagination.Button.tag}>
            <${Pagination.Button.tag} class="${baseClassName}-page-size${
                this.pageSize === this._midRows ? '-selected' : ''
            }" @click="${() => this._updatePagesize(this._midRows)}" button-size="small" type="${
                Pagination.ButtonType.Tertiary
            }" full-width>${this._midRows}</${Pagination.Button.tag}>
            <${Pagination.Button.tag} class="${baseClassName}-page-size${
                this.pageSize === this._highMidRows ? '-selected' : ''
            }" @click="${() => this._updatePagesize(this._highMidRows)}" button-size="small" type="${
                Pagination.ButtonType.Tertiary
            }" full-width>${this._highMidRows}</${Pagination.Button.tag}>
            <${Pagination.Button.tag} class="${baseClassName}-page-size${
                this.pageSize === this._maxRows ? '-selected' : ''
            }" @click="${() => this._updatePagesize(this._maxRows)}" button-size="small" type="${
                Pagination.ButtonType.Tertiary
            }" full-width>${this._maxRows}</${Pagination.Button.tag}>
            </div>
        </${Pagination.Dropdown.tag}>
        `;
    }

    private _toggle() {
        this._open = !this._open;
    }

    private _renderEllipsis(current, max) {
        const last = max,
            delta = 1,
            left = current - delta,
            right = current + delta + 1,
            range: any = [],
            rangeWithDots: any = [];

        let l;

        for (let i = 1; i <= last; i++) {
            if ((current < 5 && i <= 5) || (current > max - 4 && i >= max - 4)) {
                range.push(i);
            } else {
                if (i === 1 || i === last || (i >= left && i < right)) {
                    range.push(i);
                }
            }
        }

        for (const i of range) {
            if (l) {
                if (i - l === 2) {
                    rangeWithDots.push(l + 1);
                } else if (i - l !== 1) {
                    rangeWithDots.push('...');
                }
            }
            rangeWithDots.push(i);
            l = i;
        }

        return rangeWithDots;
    }

    private _renderArrowButton(baseClassName, callback, icon, disabled) {
        return html`
            <${Pagination.Button.tag} class="${baseClassName}-arrow" type="${
                Pagination.ButtonType.Tertiary
            }" button-size="small"  compact="true" disabled=${ifDefined(disabled || this.disabled ? true : undefined)} 
            @click=${callback}
            >
            <${Pagination.Icon.tag}
                slot="${Pagination.ButtonSlot.ICON}" 
                icon=${icon}></${Pagination.Icon.tag}>
            </${Pagination.Button.tag}>
        `;
    }

    private _renderPages(baseClassName) {
        const arr: any = [];
        const ellipsedArr = this._renderEllipsis(this.page, this._pagesNumber);
        if (this._pagesNumber > 7) {
            ellipsedArr.forEach((item) => {
                if (item === '...') {
                    arr.push(html`<div class="${baseClassName}-ellipsis">...</div>`);
                } else {
                    arr.push(
                        html`<${Pagination.Button.tag} disabled="${this.disabled}" class="${baseClassName}-page${
                            item === this.page ? '-selected' : ''
                        }" @click="${() => this._setPage(item)}" button-size="small" type="${
                            Pagination.ButtonType.Tertiary
                        }">${item}</${Pagination.Button.tag}>`
                    );
                }
            });
        } else {
            for (let i = 1; i <= this._pagesNumber; i++) {
                arr.push(
                    html`<${Pagination.Button.tag} disabled="${this.disabled}" class="${baseClassName}-page${
                        i === this.page ? '-selected' : ''
                    }" @click="${() => this._setPage(i)}" button-size="small" type="${
                        Pagination.ButtonType.Tertiary
                    }">${i}</${Pagination.Button.tag}>`
                );
            }
        }
        return arr;
    }

    // Update pages after selecting number of rows to display on one page
    private _updatePagesize(value) {
        this.pageSize = value;
        this._pagesNumber = Math.ceil(this.rows / value);
        this.page = 1;
        this.dispatchEvent(
            new CustomEvent(PaginationEvent.Pagesize, {
                bubbles: true,
                composed: true,
                detail: {
                    pageSize: this.pageSize,
                    pages: this._pagesNumber,
                    page: this.page,
                },
            })
        );
        this._open = false;
    }

    private _setPage(value) {
        this.page = value;
        this._dispatchPageEvent(this.page);
    }

    private _dispatchPageEvent(page) {
        this.dispatchEvent(
            new CustomEvent(PaginationEvent.Page, {
                bubbles: true,
                composed: true,
                detail: {
                    page,
                    pageSize: this.pageSize,
                    pages: this._pagesNumber,
                },
            })
        );
    }

    // Previous page button functionality(-1 page)
    private _previousPage(): void {
        if (this.page !== 1) {
            this.page -= 1;
        }
        this._dispatchPageEvent(this.page);
    }

    // Next page button functionality
    private _nextPage(): void {
        if (this.page !== this._pagesNumber) {
            this.page += 1;
        }
        this._dispatchPageEvent(this.page);
    }
}
